:root {
  --font-inter: "Inter", Poppins, Arial, Helvetica, sans-serif;
  --limeGreen: #1db489;
  --brightRed: #dc414c;
  --facebook: #198ff5;
  --twitter: #1ca0f2;
  --instagram-start: #fdc468;
  --instagram-end: #df4996;
  --youtube: #c4032a;
  --toggle-bg-start: #378fe6;
  --toggle-bg-end: #3eda82;
  --toggle: #aeb3cb;
  --dark-bg: #1e202a;
  --dark-top-bg: #1f212e;
  --dark-card: #252a41;
  --dark-text-1: #8b97c6;
  --dark-text-2: #fff;
  --light-bg: #fff;
  --light-top-bg: #f5f7ff;
  --light-card: #f0f2fa;
  --light-text-1: #63687e;
  --light-text-2: #1e202a;
}

html {
  font-size: 100%;
}

body {
  font-size: 1rem;
  font-family: var(--font-inter);
  font-weight: 400;
  line-height: 1.5;
}

h1,
h2,
h3,
h4 {
  line-height: 1.15;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
p,
article,
span,
section,
header,
main,
footer {
  margin: 0;
}

ul[role=list] {
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
}

a,
a:visited,
a:active {
  display: inline-block;
  text-decoration: none;
  color: inherit;
}

label,
input,
select,
textarea,
button,
optgroup {
  font: inherit;
}